import React from 'react';

export default class NewLife extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      num: 1,
    };
  }
  render() {
    return (
      <div>
        {this.state.num}
        <button
          onClick={() =>
            this.setState({
              num: Math.random() * 100,
            })
          }
        >
          change
        </button>
        <Life num={this.state.num} />
      </div>
    );
  }
}

class Life extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      number: this.props.number || 0,
    };
  }
  componentDidMount() {
    console.log('componentDidMount');
  }
  // componentWillReceiveProps(np){
  //     console.log('componentWillReceiveProps')
  //     if(np.num!==this.props.num){
  //         this.setState({
  //             number:np.num
  //         })
  //     }
  // }
  static getDerivedStateFromProps(props, state) {
    if (props.num !== state.number) {
      console.log('getDerivedStateFromProps', 'true');
      return {
        number: props.num,
      };
    }
    console.log('getDerivedStateFromProps', 'false');
    return null;
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (nextProps.num !== this.props.num) {
      console.log('shouldComponentUpdate', 'true');
      return true;
    }
    console.log('shouldComponentUpdate', 'false');
    return false;
  }
  // componentWillUpdate(){
  //     console.log('componentWillUpdate')
  // }
  getSnapshotBeforeUpdate = (prevProps, prevState) => {
    if (prevProps.num !== this.props.num) {
      console.log('getSnapshotBeforeUpdate', 'true');
      return prevProps.num;
    }
    console.log('getSnapshotBeforeUpdate', 'false');
    return null;
  };

  render() {
    console.log('render');
    return (
      <div>
        num={this.props.num}
        <br />
        number={this.state.number}
      </div>
    );
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    //snapshot getSnapshotBeforeUpdate 
    if (snapshot != null) {
      console.log('snapshot', snapshot);
    }
    console.log('componentDidUpdate');
  }
}
