import React, { useState, useEffect, memo, Suspense } from 'react';

function get() {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({
        data: [{ id: 1 }, { id: 2 }],
      });
    }, 200);
  });
}

function ShowList({ list = [] }) {
  return list.map((i) => {
    return <p key={i.id}>{i.id}</p>;
  });
}
/*
Ymui
 */
function Ym() {
  const [data, setData] = useState([]);
  useEffect(function fetch() {
    (async function() {
      const result = await get();
      setData(result.data);
    })();
  }, []);
  return <ShowList list={data} />;
}
//true
function areEqual(prevProps, nextProps) {
  if (prevProps.seconds === nextProps.seconds) {
    return true;
  } else {
    return false;
  }
}
//memoProps true
let Ymm = memo(Ym, areEqual);

const OtherComponent = React.lazy(() => import('./OtherComponent'));
const OtherComponentb = React.lazy(
  () =>
    new Promise((resolve) =>
      setTimeout(
        () =>
          resolve(
            // ES Module
            {
              // export default
              default: function render() {
                return <div>Other Componentb</div>;
              },
            },
          ),
        3000,
      ),
    ),
);
function Page({ resource }) {
  return (
    <Suspense fallback={<h1>Fetching employees....</h1>}>
      <OtherComponent />
      <OtherComponentb />
    </Suspense>
  );
}

export { Ymm as Ym, Page };
