import React from 'react';
import { HashRouter as Router, Route, Link } from 'react-router-dom';
import Hooka from './page/hooka';
import Hello from './page/hello';
import T from './page/Translate';
import Home from './page/Home';
import ViewStore from './page/ViewStore';
import UseLayout from './page/useLayoutEffect';
import Newlife from './page/Newlife';

import * as Sentry from '@sentry/browser';

export default class extends React.Component {
  componentDidCatch(error, errorInfo) {
    Sentry.withScope((scope) => {
      Object.keys(errorInfo).forEach((key) => {
        scope.setExtra(key, errorInfo[key]);
      });
      Sentry.captureException(error);
    });
  }
  componentDidMount() {
    this.props.onChange();
  }

  render() {
    return (
      <div>
        <Router>
          <T name="cc"></T>
          <Link to="/">root</Link>
          <Link to="/hooka">hooka</Link>

          <Link to="/hello/name">hello</Link>

          <Link to="/store">Store</Link>

          <Link to="/uselayout">UseLayout</Link>
          <Link to="/newlife">NewLife</Link>
          <div>
            <Route
              path="/"
              exact
              render={() => {
                return <Home />;
              }}
            />
            <Route path="/hello/:name" component={Hello} />
            <Route path="/hooka" component={Hooka} />
            <Route path="/store" component={ViewStore} />
            <Route path="/uselayout" component={UseLayout} />
            <Route path="/newlife" component={Newlife} />
          </div>
        </Router>
      </div>
    );
  }
}
